<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 提示信息 -->
            <a-form-model-item
              label="设备家族名称"
              prop="resourceFamilyName"
              ref="resourceFamilyName"
            >
              <a-input
                v-model="entity.resourceFamilyName"
                placeholder="请输入设备家族名称"
                name="resourceFamilyName"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item
              label="设备分类"
              prop="resourceCategory"
              ref="resourceCategory"
            >
              <a-select
                v-model="entity.resourceCategory"
                placeholder="请选择设备分类"
                name="resourceCategory"
              >
                <a-select-option value="网络设备">网络设备</a-select-option>
                <a-select-option value="存储设备">存储设备</a-select-option>
                <a-select-option value="计算设备">计算设备</a-select-option>
                <a-select-option value="安全设备">安全设备</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model:value="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 最近修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
export default {
  name: "CreateOrEditResourceFamily",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        resourceFamilyName: "",
        resourceCategory: "",
        description: "",
        createdBy: "",
        modifiedTime: ""
      },
      rules: {
        resourceFamilyName: [
          { required: true, message: "设备家族名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            trigger: "change",
            message: "名称格式不正确，不能包含前后空格",
          },
        ],
        resourceCategory: [
          { required: true, message: "设备分类不能为空", trigger: "change" }
        ]
      },
    };
  },

  mounted() {},

  methods: {
    // 初始化
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取设备家族数据
        setTimeout(() => {
          this.loading = false;

          this.entity = {
            resourceFamilyName: "交换机",
            resourceCategory: "网络设备",
            description: "网络交换设备，用于网络数据的传输和交换",
            createdBy: "admin",
            modifiedTime: "2025-03-18 11:55"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    resetFormData() {
      // 重置表单绑定的数据源
      this.entity = {
        resourceFamilyName: "",
        resourceCategory: "",
        description: "",
        createdBy: "",
        modifiedTime: ""
      };
    },
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.resourceFamilyName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e){
      console.log(e.target.value);
    }
  },
};
</script>

<style lang="scss" scoped></style> 